<template>
  <div class="actmembermanage">
    <Headerbar login="false"></Headerbar>
    <div class="topbar">
      <div class="left">
        <div class="backbtn" v-if="currenteventinfo">
          <router-link
            :to="{
    name:'ActivityPage', 
    params: { hostid: currenteventinfo[17]}
    }"
          >
            <i class="ri-arrow-left-s-line"></i>
          </router-link>
        </div>
        <div class="actname">
          <span v-if="currenteventinfo">{{currenteventinfo[0]}} - 報名清單</span>
        </div>
      </div>
      <div class="right">
        <div class="optionblock">
          <div class="exportlistbtn" @click="toexcel">
            <Iconbtn icon="ri-file-download-line" btnname="匯出報名名單"></Iconbtn>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="actblockwrap">
        <Actblock
          v-if="currenteventinfo"
          :actname="currenteventinfo[0]"
          :isprivate="currenteventinfo[5]"
          :isfree="currenteventinfo[12]"
          :acttype="currenteventinfo[6]"
          :describe="currenteventinfo[11]"
          :taglist="currenteventinfo[9]"
          :time="currenteventinfo[3]"
          :date="convertdate(currenteventinfo[2])"
          :pos="currenteventinfo[4]"
          :quota="currenteventinfo[7]"
          :actpic="currenteventinfo[1]"
          :price="currenteventinfo[13]"
        ></Actblock>
      </div>
      <div class="actmemberlist">
        <div class="top">
          <div class="searchwrap">
            <Searchbar placehold="搜尋報名人員" @emitkeyword="search"></Searchbar>
          </div>
          <div class="actinpeople">已報名：{{this.listinfo.length}}人</div>
        </div>

        <div class="listtab">
          <!-- <div v-for="(item,index) in tablist" :key="index">
                    <div class="tab1" @click="clickedtab(index,item)">
                        <span>{{item}}</span>
                        <div class="underline" :class="{clicked:index==currenttabindex}"></div>
                    </div>
          </div>-->
        </div>
        <div class="list">
          <div class="tablewrap">
            <table class="header"></table>
            <table id="myTable2">
              <tr class="header">
                <th width="150px">報名人員編號</th>
                <th width="150px" v-for="(item,regisindex) of regisinfo" :key="regisindex">{{item}}</th>
              </tr>
              <tr class="listitem" v-for="(item,upindex) in paginatedata" :key="upindex">
                <td>{{(upindex+1)+pagenumber*pagesize}}</td>
                <td
                  v-for="(listitem,numindex)  of item"
                  :key="numindex"
                  @click="openmemberinfo(upindex)"
                  v-show="listitem!=eventid&&numindex!=item.length-1"
                >{{listitem}}</td>
              </tr>
            </table>
            <tr
              class="csv listitem"
              v-for="(item,allindex) in filtermemberlist"
              :key="allindex"
              v-show="false"
            >
