<script>
import Headerbar from "../components/Header";
import Actblock from "../components/Actblock";
import Searchbar from "../components/Searchbar";
import DeleMemberPopup from "../components/DeleMemberPopup";
// import gsap from "gsap";
import axios from "axios";
// import anime from 'animejs';
import Iconbtn from "../components/Iconbtn";

import Actmemberinfopage from "../components/ActMemberinfo";
export default {
  data() {
    return {
      isclick: false,
      tablist: ["全部", "已報名", "已取消"],
      currenttabindex: 0,
      // regisinfo: [],
      listinfo: [],
      testlist: [],
      isopen: false,
      clickmemberid: 0,
      propsisopen: false,
      currentstatus: "全部",
      keyword: "",
      loadingcircle: false,
      pagenumber: 0,
      pagesize: 6,
      currenteventinfo: null,
      hasdata: false,
      eventid: this.$route.params.eventid,
      delemembershow: false
    };
  },
  created() {
    if (window.sessionStorage.getItem("storeeventinfo")) {
      var storeeventinfo = window.sessionStorage.getItem("storeeventinfo");
      this.currenteventinfo = JSON.parse(storeeventinfo);
    }
    console.log(this.currenteventinfo);

    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbw-x-8FmQJA4hH2eCgBy2iFK619Hsuk_osk7pFbeB57uJVV-qaB0GL5aONFLtxWYYbnnw/exec",
      params: {
        mode: "user",
        id: this.$route.params.eventid
      }
    })
      .then(response => {
        console.log(response.data);
        this.listinfo = response.data;
      })
      .catch(error => {
        console.error(error);
      });
  },
  mounted() {},
  methods: {
    closedele() {
      this.delemembershow = false;
    },
    showdelepopup() {
      (this.isopen = false), (this.delemembershow = true);
    },
    toexcel() {
      var titleList = [];
      var csvList = [];

      let tableheader = document.querySelectorAll("th");
      tableheader.forEach(function(item) {
        titleList.push(item.innerHTML);
      });
      csvList.push(titleList);
      let tablecontent = document.querySelectorAll("tr.csv");
      // console.log('所有資料',tablecontent)
      // 取得所有資料
      tablecontent.forEach(function(tableitem, index) {
        var regList = [];
        // console.log('個別資料',item.children.length)
        if (index > 0) {
          tableitem.children.forEach(function(item, index) {
            if (index >= 0 && index < tableitem.children.length - 2) {
              regList.push(item.innerHTML);
            }
          });
        }
        csvList.push(regList);
      });
      csvList.splice(1, 1);
      // 產生 csv 內容
      let csv;
      csvList.forEach(row => {
        csv += row.join(",");
        csv += "\n";
      });
      console.log(csvList);
      // 產生 csv 連結
      const anchor = document.createElement("a");
      anchor.href =
        "data:text/csv;charset=utf-8,\uFEFF" + encodeURIComponent(csv);
      // anchor.href = csvContent;
      anchor.target = "_blank";
      anchor.download = `${this.currenteventinfo[0]}報名清單.csv`;
      anchor.click();
    },
    clickedtab(index, status) {
      this.currentstatus = status;
      this.currenttabindex = index;
    },
    openmemberinfo(id) {
      this.isopen = true;
      this.clickmemberid = id+(this.pagenumber*this.pagesize);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.propsisopen = true;
    },
    closememberifo() {
      this.isopen = false;
      document.body.style.overflow = "visible";
    },
    search(val) {
      this.keyword = val;
      console.log(val);
    },
    uppage() {
      this.pagenumber--;
    },
    nextpage() {
      this.pagenumber++;
    },
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    },
    convertphone(phone, index) {
      let str = phone;
      if (index == "5") {
        return (str = "0" + str);
      } else {
        return str;
      }
    }
  },
  computed: {
    filtermemberlist: {
      get() {
        return this.listinfo.filter(item => {
          console.log(item[0]);
          if (this.keyword == "") {
            return this.listinfo;
          } else if (item[0].search(this.keyword) != -1) {
            return item;
          }
        });
      }
    },
    pagecount() {
      let l = this.filtermemberlist.length;
      let s = this.pagesize;
      return Math.floor(l / s);
    },
    paginatedata() {
      const start = this.pagenumber * this.pagesize;
      const end = start + this.pagesize;

      return this.filtermemberlist.slice(start, end);
    },
    regisinfo() {
      return this.currenteventinfo[10].split("、");
    },
    userinfo() {
      return this.$store.state.userinfo;
    }
  },
  components: {
    Headerbar,
    Actblock,
    Searchbar,
    Actmemberinfopage,
    Iconbtn,
    DeleMemberPopup
  }
};
</script>

